//
axios.defaults.baseURL = "http://localhost:51601/user";    /**/
axios.defaults.timeout = 5000000    /**/

import axios from "axios";
import qs from 'qs';
import store from "@/stores/store";

//get
function doGet(url, params) {
    return axios({
        url: url,
        method: "get",
        params: params
    });
}

/*json*/
function doPostJson(url, params) {
    return axios({
        url: url,
        method: "post",
        data: params
    });
}

//
function doPost(url, params) {
    let requestData = qs.stringify(params)
    return axios.post(url, requestData)
}


//
axios.interceptors.request.use(function (config) {

    //urltoken

    let token;
    if (config.url == '/api/v1/user/information') {
        store.getToken().then(resp => {
            console.log(resp)
            console.log("url=====" + config.url);
            token = resp;

            config.headers['token'] = token;

        });
    }
    return config;
}, function (err) {
    console.log("" + err)
})


export {doGet, doPostJson, doPost}



